.wrapper {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    left: 0;
    right: 0;
    bottom: 50px;
}

.gap {
    margin-right: 30px;
}

@media (min-width: 1100px) {
    .wrapper {
        right: 70px;
        left: auto;
    }
}