.switcherWrapper {
    display: flex;
    flex-direction: column;
    position: absolute;
    right: 70px;
}

.switcherItem {
    cursor: pointer;
    width: 6px;
    height: 27px;
    background: rgba(255,255,255,0.5);
    margin-bottom: 10px;
    border-radius: 3px;
    transition: all 1s;
}